@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed Regular"), local("Roboto Condensed"), url("../fonts/robotocondensed-400-normal.woff2") format("woff2"), url("../fonts/robotocondensed-400-normal.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("Roboto Condensed"), url("../fonts/robotocondensed-700-normal.woff2") format("woff2"), url("../fonts/robotocondensed-700-normal.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PT Mono Regular"), local("PT Mono"), url("../fonts/ptmono-400-normal.woff2") format("woff2"), url("../fonts/ptmono-400-normal.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Regular"), local("Roboto"), url("../fonts/roboto-500-normal.woff2") format("woff2"), url("../fonts/roboto-500-normal.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%); }

.no-default-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  line-height: 16px;
  display: block;
  padding: 14px 14px 13px;
  cursor: pointer;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  border: 1px solid #00b2ff;
  border-radius: 5px;
  background-color: transparent; }

.page-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #1b2931; }

.page-header__img-container {
  margin-left: auto;
  width: 705px;
  height: 515px; }
  @media (max-width: 1023px) {
    .page-header__img-container {
      width: 260px;
      height: 340px; } }
  @media (max-width: 767px) {
    .page-header__img-container {
      width: 294px;
      height: 220px; } }

.contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.contacts__item {
  color: #00b2ff; }

.contacts__item-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 20px;
  vertical-align: top;
  fill: #00b2ff; }

.contacts__button {
  width: 180px; }
